var _self;

export default{
	
	data() {
		return {
			loginInfo:{},
			inviteCode:"",
			isGet:0,
			isVip:0
		}
	},
	onLoad(opt) {
		_self = this;
		
		var loginRes = this.checkLogin('../index/recommend', '2');
		if(!loginRes){return false;}
		this.loginInfo = loginRes;
		this.getData();
	},
	components:{
		
	},
	methods:{
		//扫取邀请码
		scanInviteCode(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
					if(res.result.indexOf("jfcm_tjm") != -1){
						_self.inviteCode = res.result.split("=")[1]
					}else{
						_self.tipMsg("请扫描正确的邀请码");
					}
					
			    }
			});

		},
		goPakPage(){
			uni.navigateTo({
				url:"product_pak"
			})
		},
		//下一步
		goNextStep(i){
			
			if(i == 0){
				
				uni.request({
				    url: _self.$httpUrl+'index.php/api/recommend/dobuy', 
					method:"POST",
					data:{
						user_id:_self.loginInfo.uid,
						invitecode:_self.inviteCode
					},
				    success: (res) => {
				     
				      console.log(res);
					  
					  if(res.data.status == 1){
						
							uni.navigateTo({
								url:"../pay/pay_index?orderid="+res.data.result.order_sn + "&paynum=399"
							})
						
					  }else{
						  uni.showToast({
						  	title:res.data.msg,
							icon:""
						  })
					  }
					  
				    },fail: (e) => {
						
				    },complete: (e) => {
						
				    }
				});
				
				
				
			}else if(i == 1){
				uni.navigateTo({
					url:"product_pak"
				})
			}else if(i == 2){
				uni.navigateTo({
					url:"look_order"
				})
			}
			
			
		},
		getData(){
			
			uni.request({
			    url: _self.$httpUrl+'index.php/api/recommend/buyvip', 
				method:"POST",
				data:{
					user_id:_self.loginInfo.uid
				},
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					
					var entity = res.data.result;
					//1代表领取过了0 代表没有领取
					_self.isGet = entity.ordercount;
					//0普通会员 非普通会员皆可领
					_self.isVip = entity.userinfo.levelid;
					
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
		},
		changePayType(i){
			_self.currentIndex = i;
		},
		lookRuleDetail(){
			var mbr = _self.isVip == 0 ? "会员" : "非会员"
			uni.navigateTo({
				url:"member_upgrade2?mbr="+mbr
			})
		},
		backBeforePage: ()=>{
			
			uni.navigateBack({
				delta:1
			})
		}
		
		
	}
	
	
	
}

